'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import Sidebar from '@/components/Sidebar';
import PatientSidebar from '@/components/PatientSidebar';
import Loading from '@/components/Loading';
import api from '@/lib/api';
import Link from 'next/link';
import { FiStar, FiMapPin, FiClock, FiDollarSign, FiBell, FiSearch, FiMessageCircle, FiMenu, FiX } from 'react-icons/fi';

export default function DoctorsPage() {
  const { user, loading: authLoading, logout } = useAuth();
  const router = useRouter();
  const [doctors, setDoctors] = useState<any[]>([]);
  const [departments, setDepartments] = useState<any[]>([]);
  const [selectedDept, setSelectedDept] = useState<string>('');
  const [searchQuery, setSearchQuery] = useState<string>('');
  const [suggestions, setSuggestions] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [nextCursor, setNextCursor] = useState<string | null>(null);
  const [hasMore, setHasMore] = useState(false);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  useEffect(() => {
    if (!authLoading && !user) {
      router.push('/login');
    }
  }, [user, authLoading, router]);

  useEffect(() => {
    if (user) {
      fetchDepartments();
      fetchDoctors();
    }
  }, [user, selectedDept, searchQuery]);

  useEffect(() => {
    if (searchQuery.length >= 2) {
      searchDoctors();
    } else {
      setSuggestions([]);
      setShowSuggestions(false);
    }
  }, [searchQuery]);

  const fetchDepartments = async () => {
    try {
      const response = await api.get('/departments');
      setDepartments(response.data.departments);
    } catch (error) {
      console.error('Error fetching departments:', error);
    }
  };

  const fetchDoctors = async (cursor?: string) => {
    try {
      setLoading(true);
      const params: any = { limit: 12 };
      if (selectedDept) params.departmentId = selectedDept;
      if (searchQuery && searchQuery.length >= 2) params.search = searchQuery;
      if (cursor) params.cursor = cursor;

      const response = await api.get('/doctors', { params });
      if (cursor) {
        setDoctors((prev) => [...prev, ...response.data.doctors]);
      } else {
        setDoctors(response.data.doctors);
      }
      setNextCursor(response.data.pagination.nextCursor);
      setHasMore(response.data.pagination.hasMore);
    } catch (error) {
      console.error('Error fetching doctors:', error);
    } finally {
      setLoading(false);
    }
  };

  const searchDoctors = async () => {
    try {
      const response = await api.get('/doctors/search', {
        params: { query: searchQuery, limit: 5 },
      });
      setSuggestions(response.data.doctors);
      setShowSuggestions(true);
    } catch (error) {
      console.error('Error searching doctors:', error);
    }
  };

  const handleSuggestionClick = (doctor: any) => {
    setSearchQuery(doctor.user.name);
    setShowSuggestions(false);
    setSelectedDept(doctor.departmentId);
  };

  const handleSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchQuery(e.target.value);
  };

  const loadMore = () => {
    if (nextCursor && !loading) {
      fetchDoctors(nextCursor);
    }
  };

  if (authLoading) {
    return <Loading />;
  }

  if (!user) {
    return null;
  }

  // Patient view with new design
  if (user.role === 'patient') {
    return (
      <div className="flex min-h-screen bg-gray-50">
        <PatientSidebar user={user} logout={logout} />
        <main className="ml-64 flex-1 transition-all duration-300">
          {/* Header */}
          <header className="bg-white border-b border-gray-200 px-8 py-4 flex items-center justify-between">
            <h1 className="text-2xl font-bold text-gray-800 uppercase tracking-wide">FIND A DOCTOR</h1>
            <div className="flex items-center gap-4">
              <button className="relative p-2 hover:bg-gray-100 rounded-lg transition-colors">
                <FiSearch className="text-xl text-gray-600" />
              </button>
              <Link href="/patient/chat" className="relative p-2 hover:bg-gray-100 rounded-lg transition-colors">
                <FiMessageCircle className="text-xl text-gray-600" />
              </Link>
              <button className="relative p-2 hover:bg-gray-100 rounded-lg transition-colors">
                <FiBell className="text-xl text-gray-600" />
              </button>
            </div>
          </header>
          <div className="p-8">
            <div className="max-w-7xl mx-auto">

          <div className="mb-6 flex flex-col md:flex-row gap-4">
            <div className="flex-1 relative">
              <input
                type="text"
                value={searchQuery}
                onChange={handleSearchChange}
                onFocus={() => searchQuery.length >= 2 && setShowSuggestions(true)}
                placeholder="Search doctors by name..."
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-teal-500 focus:border-transparent"
              />
              {showSuggestions && suggestions.length > 0 && (
                <div className="absolute z-10 w-full mt-1 bg-white border border-gray-200 rounded-lg shadow-lg max-h-60 overflow-y-auto">
                  {suggestions.map((doctor) => (
                    <div
                      key={doctor.id}
                      onClick={() => handleSuggestionClick(doctor)}
                      className="px-4 py-2 hover:bg-teal-50 cursor-pointer border-b border-gray-100 last:border-0"
                    >
                      <p className="font-semibold text-gray-800">{doctor.user.name}</p>
                      <p className="text-sm text-gray-600">{doctor.specialization}</p>
                      <p className="text-xs text-gray-500">{doctor.department?.name}</p>
                    </div>
                  ))}
                </div>
              )}
            </div>
            <select
              value={selectedDept}
              onChange={(e) => setSelectedDept(e.target.value)}
              className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-teal-500 focus:border-transparent"
            >
              <option value="">All Departments</option>
              {departments.map((dept) => (
                <option key={dept.id} value={dept.id}>
                  {dept.icon} {dept.name}
                </option>
              ))}
            </select>
          </div>

          {loading && doctors.length === 0 ? (
            <Loading />
          ) : (
            <>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {doctors.map((doctor) => (
                  <div
                    key={doctor.id}
                    className="bg-white rounded-lg shadow-md p-6 hover:shadow-lg transition-shadow animate-fadeIn"
                  >
                    <div className="flex items-start justify-between mb-4">
                      <div>
                        <h3 className="text-xl font-semibold text-gray-800">
                          {doctor.user?.name}
                        </h3>
                        <p className="text-teal-600 mt-1">{doctor.specialization}</p>
                        <p className="text-sm text-gray-600 mt-1">
                          {doctor.department?.name}
                        </p>
                      </div>
                      {doctor.department?.icon && (
                        <span className="text-3xl">{doctor.department.icon}</span>
                      )}
                    </div>

                    {doctor.bio && (
                      <p className="text-gray-600 text-sm mb-4 line-clamp-2">{doctor.bio}</p>
                    )}

                    <div className="space-y-2 mb-4">
                      {doctor.experience && (
                        <div className="flex items-center text-sm text-gray-600">
                          <FiClock className="mr-2" />
                          {doctor.experience} years experience
                        </div>
                      )}
                      {doctor.consultationFee && (
                        <div className="flex items-center text-sm text-gray-600">
                          <FiDollarSign className="mr-2" />
                          ${doctor.consultationFee}
                        </div>
                      )}
                    </div>

                    <Link
                      href={`/doctors/${doctor.id}`}
                      className="block w-full text-center bg-teal-600 text-white py-2 rounded-lg font-semibold hover:bg-teal-700 transition-colors"
                    >
                      View Profile
                    </Link>
                  </div>
                ))}
              </div>

              {hasMore && (
                <div className="mt-8 text-center">
                  <button
                    onClick={loadMore}
                    disabled={loading}
                    className="px-6 py-3 bg-indigo-600 text-white rounded-lg font-semibold hover:bg-indigo-700 transition-colors disabled:opacity-50"
                  >
                    {loading ? 'Loading...' : 'Load More'}
                  </button>
                </div>
              )}
            </>
          )}
            </div>
          </div>
        </main>
      </div>
    );
  }

  // Admin/Other roles view
  return (
    <div className="flex min-h-screen">
      <Sidebar />
      <main className="ml-64 flex-1 p-8">
        <div className="max-w-7xl mx-auto">
          <h1 className="text-3xl font-bold text-gray-800 mb-6">Find a Doctor</h1>

          <div className="mb-6 flex flex-col md:flex-row gap-4">
            <div className="flex-1 relative">
              <input
                type="text"
                value={searchQuery}
                onChange={handleSearchChange}
                onFocus={() => searchQuery.length >= 2 && setShowSuggestions(true)}
                placeholder="Search doctors by name..."
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-teal-500 focus:border-transparent"
              />
              {showSuggestions && suggestions.length > 0 && (
                <div className="absolute z-10 w-full mt-1 bg-white border border-gray-200 rounded-lg shadow-lg max-h-60 overflow-y-auto">
                  {suggestions.map((doctor) => (
                    <div
                      key={doctor.id}
                      onClick={() => handleSuggestionClick(doctor)}
                      className="px-4 py-2 hover:bg-teal-50 cursor-pointer border-b border-gray-100 last:border-0"
                    >
                      <p className="font-semibold text-gray-800">{doctor.user.name}</p>
                      <p className="text-sm text-gray-600">{doctor.specialization}</p>
                      <p className="text-xs text-gray-500">{doctor.department?.name}</p>
                    </div>
                  ))}
                </div>
              )}
            </div>
            <select
              value={selectedDept}
              onChange={(e) => setSelectedDept(e.target.value)}
              className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-teal-500 focus:border-transparent"
            >
              <option value="">All Departments</option>
              {departments.map((dept) => (
                <option key={dept.id} value={dept.id}>
                  {dept.icon} {dept.name}
                </option>
              ))}
            </select>
          </div>

          {loading && doctors.length === 0 ? (
            <Loading />
          ) : (
            <>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {doctors.map((doctor) => (
                  <div
                    key={doctor.id}
                    className="bg-white rounded-lg shadow-md p-6 hover:shadow-lg transition-shadow animate-fadeIn"
                  >
                    <div className="flex items-start justify-between mb-4">
                      <div>
                        <h3 className="text-xl font-semibold text-gray-800">
                          {doctor.user?.name}
                        </h3>
                        <p className="text-teal-600 mt-1">{doctor.specialization}</p>
                        <p className="text-sm text-gray-600 mt-1">
                          {doctor.department?.name}
                        </p>
                      </div>
                      {doctor.department?.icon && (
                        <span className="text-3xl">{doctor.department.icon}</span>
                      )}
                    </div>

                    {doctor.bio && (
                      <p className="text-gray-600 text-sm mb-4 line-clamp-2">{doctor.bio}</p>
                    )}

                    <div className="space-y-2 mb-4">
                      {doctor.experience && (
                        <div className="flex items-center text-sm text-gray-600">
                          <FiClock className="mr-2" />
                          {doctor.experience} years experience
                        </div>
                      )}
                      {doctor.consultationFee && (
                        <div className="flex items-center text-sm text-gray-600">
                          <FiDollarSign className="mr-2" />
                          ${doctor.consultationFee}
                        </div>
                      )}
                    </div>

                    <Link
                      href={`/doctors/${doctor.id}`}
                      className="block w-full text-center bg-teal-600 text-white py-2 rounded-lg font-semibold hover:bg-teal-700 transition-colors"
                    >
                      View Profile
                    </Link>
                  </div>
                ))}
              </div>

              {hasMore && (
                <div className="mt-8 text-center">
                  <button
                    onClick={loadMore}
                    disabled={loading}
                    className="px-6 py-3 bg-indigo-600 text-white rounded-lg font-semibold hover:bg-indigo-700 transition-colors disabled:opacity-50"
                  >
                    {loading ? 'Loading...' : 'Load More'}
                  </button>
                </div>
              )}
            </>
          )}
        </div>
      </main>
    </div>
  );
}

